.App {
  text-align: center;
}

.divTable{
  display: table;
  max-width: 20em;
  float: right;
  display: flex;
  align-items: center;
}

.divTableRow{
  display: table-row;
}

.divTableHeading{
  background-color: white;
  display: table-header-group;
  font-weight: 600;
}

.divTableCell,
.divTableHead{
  border: 1px solid #999999;
  display: table-cell;
  padding: 3px 10px;
}

.divTableFoot{
  background-color: white;
  display: table-footer-group;
  font-weight: bold; 
}

.divTableBody{
  display: table-row-group;
}

.divTableCell:nth-child(1n){
  background-color: white;
}

.container{
  display: flex;
  opacity: 0.8;
  background-color: blanchedalmond;
  justify-content: center;
}

img{
  height: 16em;
}

input{
  box-sizing: border-box;
  font-size: large;
  padding: 1%;
  display: block;
  margin: 4% auto;
}

body{
  background-image: url("https://images.unsplash.com/photo-1542779283-429940ce8336?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1050&q=80");
  background-size: cover;
}